@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  background: #000;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container, .circle, .rating-names, .stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 550px;
  height: 400px;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
}

.container h1 {
  text-transform: uppercase;
  word-spacing: 5px;
  margin-top: -20px;
  font-size: 40px;
}

.circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}

.circle .emojis {
  gap: 20px;
  transition: all .3s ease-in-out;
  display: flex;
  transform: translateX(180px);
}

.circle .emojis img {
  width: 70px;
}

.rating-names {
  text-align: center;
  width: 150px;
  height: 40px;
  color: #fff;
  background-color: #000;
  border-radius: 20px;
  line-height: 40px;
  overflow: hidden;
}

.rating-names .names {
  transition: all .3s ease-in-out;
  transform: translateY(80px);
}

.stars {
  gap: 30px;
  margin-top: 20px;
}

.stars span {
  color: #000;
  cursor: pointer;
  font-size: 50px;
}

